import React, { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import { FormInstance } from 'rsuite/lib/Form';
import _ from 'lodash';
import { Alert } from 'rsuite';
import { Schema } from 'schema-typed';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import template from '@/utils/template';

interface DrawerDisplayProps {
  show: boolean;
  loading: boolean;
  submitting: boolean;
  isEdit: boolean;
}

interface Props {
  defaultFormData: Record<string, any>;
  api: Url;
  model?: Schema;
  mapFromResponse?: Function;
  mapToRequest?: Function;
  mapErrors?: (rspErrors: Record<string, any[]>) => Record<string, string>;
  submitSuccessCallback?: Function;
  submitErrorCallback?: Function;
  fetchEditCallback?: Function;
}

export interface Result {
  drawer: DrawerDisplayProps;
  formData: Record<string, any>;
  originalFormData: Record<string, any>;
  formErrors: Record<string, any>; // field
  formRef: React.RefObject<FormInstance>;
  handleFormChange: Function; // 
  handleFormCreate: Function; // 
  handleFormEdit: Function; // 
  handleFormClose: Function; // 
  handleFormSubmit: Function; // 
  setFormErrors:
    | ((errors: Record<string, any>) => void)
    | ((callback: (prevErrors: Record<string, any>) => Record<string, any>) => void); // 
  checkForm: (
    fieldNameOrData?: string | Record<string, any>,
    value?: any,
    data?: Record<string, any>
  ) => Promise<any>; // model
  setDrawer: Function; // drawer
}

export default function useDrawerForm({
  defaultFormData = {},
  api,
  model,
  mapFromResponse,
  mapToRequest,
  mapErrors,
  submitSuccessCallback,
  submitErrorCallback,
  fetchEditCallback
}: Props): Result {
  const [drawer, setDrawer] = useState<DrawerDisplayProps>({
    show: false,
    loading: false,
    submitting: false,
    isEdit: false
  });

  // 
  const initialFormData = useMemo(() => {
    const schema = _.get(model, 'schema', {});
    const autoFilledData = _.entries(schema)
      .filter(([key, type]) => type.name === 'string')
      .reduce((acc, [key, type]) => {
        switch (type.name) {
          case 'string':
            (acc as any)[key] = '';
            break;
          default:
        }
        return acc;
      }, {});
    return {
      ...autoFilledData,
      ...defaultFormData
    };
  }, [defaultFormData, model]);

  const [formData, setFormData] = useState(initialFormData);
  const [originalFormData, setOriginalFormData] = useState({});

  const [formErrors, setFormErrors] = useState({});

  const formRef = useRef<FormInstance>(null);

  /**
   * 
   * stringfieldNameobjectdata
   * stringfieldNamevaluevalue
   * data
   *
   * Promisethen
   *   
   */
  const checkForm = useCallback(
    (fieldNameOrData?: string | Record<string, any>, value?: any, data?: Record<string, any>) => {
      if (_.isEmpty(fieldNameOrData)) {
        // 
        return formRef.current
          ? formRef.current.checkAsync().then(({ formError }) => {
              setFormErrors(formError);
              return formError;
            })
          : Promise.resolve({}); // 
      }

      if (_.isNil(value) || _.isObject(fieldNameOrData)) {
        // object
        if (_.isString(fieldNameOrData)) {
          return formRef.current
            ? formRef.current.checkForFieldAsync(fieldNameOrData).then(checkResult => {
                setFormErrors(prevState => ({
                  ...prevState,
                  [fieldNameOrData]: checkResult.errorMessage
                }));
                return checkResult.errorMessage;
              })
            : Promise.resolve({}); // 
        }
        if (_.isObject(fieldNameOrData)) {
          return model
            ? model.checkAsync(fieldNameOrData).then(checkResults =>
                _.toPairs(checkResults).reduce(
                  (acc, [name, { hasError, errorMessage }]) =>
                    hasError
                      ? {
                          ...acc,
                          [name]: errorMessage
                        }
                      : acc,
                  {}
                )
              )
            : Promise.resolve({});
        }
      }

      return model
        ? model.checkForFieldAsync(fieldNameOrData, value, data).then(checkResult => {
            return checkResult.errorMessage;
          })
        : Promise.resolve({});
    },
    [model]
  );

  const getFetchMethod = (method: any, url: any) =>
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useFetch<CommonResponse>(method, url, {
      cacheParams: true,
      params: typeof url !== 'string' && url.getKeys().includes('var') ? { var: 'json' } : {}
    }).onFetch;

  const onOpenEdit = getFetchMethod(FETCH_METHOD.GET, template`${api}/${'id'}`);
  const onCreateSubmit = getFetchMethod(FETCH_METHOD.POST, api);
  const onEditSubmit = getFetchMethod(FETCH_METHOD.PUT, template`${api}/${'id'}`);

  const handleFormChange = useCallback((val, key) => {
    if (_.isFunction(val)) {
      /**
       * setkey _.isObject
       * depsformData
       *   FormDataformData
       *  /hac
       */
      setFormData(val);
    } else if (_.isString(key)) {
      setFormData(prevState => ({
        ...prevState,
        [key]: val
      }));
    } else if (_.isObject(val)) {
      setFormData(val);
    }
  }, []);

  const handleFormCreate = useCallback(() => {
    setDrawer({
      show: true,
      loading: false,
      submitting: false,
      isEdit: false
    });
    setFormErrors({});
    setFormData(initialFormData);
  }, [initialFormData]);

  const handleFormEdit = useCallback(
    params => {
      setFormData(initialFormData); // 
      setDrawer({
        show: true,
        loading: true,
        submitting: false,
        isEdit: true
      });
      onOpenEdit({ params })
        .then(response => {
          let data = _.get(response, 'data', {});
          fetchEditCallback && fetchEditCallback(data);
          _.isFunction(mapFromResponse) && (data = mapFromResponse(data));
          //idformdatacpoid
          setFormData({
            id: _.get(params, 'id'),
            ...initialFormData,
            ...data
          });
          setOriginalFormData(data);
        })
        .then(() => {
          setDrawer({
            show: true,
            loading: false,
            submitting: false,
            isEdit: true
          });
        });
    },
    [mapFromResponse, fetchEditCallback, initialFormData]
  );

  const handleFormClose = useCallback(() => {
    setDrawer({
      show: false,
      loading: false,
      submitting: false,
      isEdit: false
    });
    setFormErrors({});
    setFormData(initialFormData);
  }, [initialFormData]);

  const onCheckSuccess = useCallback(() => {
    const requestData = _.isFunction(mapToRequest)
      ? mapToRequest(formData, drawer.isEdit)
      : formData;
    const onSubmit = drawer.isEdit ? onEditSubmit : onCreateSubmit; // fetch
    const option = _.extend(
      { data: requestData },
      drawer.isEdit
        ? {
            params: { id: _.get(requestData, 'id') }
          }
        : {}
    );

    onSubmit(option)
      .then(rsp => {
        // 
        setDrawer(prevState => ({
          ...prevState,
          submitting: false
        }));

        // rsp.errors{field1: [...errorsOfField1], field2: [errorsOfField2]}
        // fieldmapErrors
        const errors =
          _.isFunction(mapErrors) && rsp.errors
            ? mapErrors(rsp.errors)
            : _.mapValues(rsp.errors, v => v.join('; '));

        return _.isEmpty(errors) ? Promise.resolve() : Promise.reject(errors);
      })
      .then(() => {
        // drawer
        setDrawer(prevState => ({
          ...prevState,
          show: false
        }));
        submitSuccessCallback && submitSuccessCallback();
      })
      .catch(err => {
        setFormErrors(err);
        setDrawer(prevState => ({
          ...prevState,
          submitting: false
        }));
        submitErrorCallback && submitErrorCallback(err);
        // _.forIn(err, val => {
        //   Alert.error(val);
        // });
      });
  }, [formData, drawer, submitErrorCallback]);

  const handleFormSubmit = useCallback(() => {
    setDrawer(prevState => ({
      ...prevState,
      submitting: true
    }));

    // 
    checkForm().then(errors => {
      if (_.every(_.values(errors), _.isEmpty)) {
        onCheckSuccess();
      } else {
        setDrawer(prevState => ({
          ...prevState,
          submitting: false
        }));
      }
    });
  }, [formData, drawer]);

  return {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormEdit,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm,
    setDrawer
  };
}
