import _ from 'lodash';
import React from 'react';
import classnames from 'classnames';
import { PickerDataConfig, toPickerData } from '@/utils/pickerMapper';

/**
 * Picker
 * 
 * *namelabellabelKey
 * labelKeyname
 * renderValuerenderMenuItemrenderTreeNodelabelKey
 * **
 */

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

export interface UseSearchProps {
  searchKeys?: string[]; // data
  isTree?: boolean; // data
  notFormat?: boolean; // 
  mapLabel?: PickerDataConfig['mapLabel'];
  data?: any[];
  labelKey?: string;
  valueKey?: string;
  childrenKey?: string;
  renderNodeWithTitle?: boolean;
  renderValue?: Function;
  renderMenuItem?: Function;
  renderTreeNode?: Function;
}

export interface OutProps {
  data: any[];
  labelKey: string;
  valueKey: string;
  childrenKey: string;
  renderValue: Function;
  renderMenuItem?: Function;
  renderTreeNode?: Function;
}

const { useCallback, useMemo } = React;

const useSearchForPicker: PropMapper<UseSearchProps, OutProps> = ({
  searchKeys = [],
  isTree = false,
  notFormat = false,
  mapLabel,
  data = [],
  labelKey = 'label',
  valueKey = 'value',
  childrenKey = 'children',
  renderNodeWithTitle = true,
  renderValue,
  renderMenuItem,
  renderTreeNode,
  ...props
}) => {
  const getTransformedData = () => {
    // namesearchKeyslabelKeyvalueKey
    if (!isTree) {
      return data.map(i => ({
        ..._.omit(i, 'children'),
        name: searchKeys.map(k => i[k]).join(' * '),
        label: i[labelKey],
        value: i[valueKey]
      }));
    }

    // {name, label, value, children}
    if (notFormat) {
      return toPickerData(data, {
        labelKey,
        valueKey,
        childrenKey,
        nameKeys: searchKeys.filter(i => i !== labelKey).concat(labelKey),
        noSplice: true,
        mapLabel
      });
    }

    // 
    return data;
  };

  const transformedData = useMemo(() => getTransformedData(), [isTree, notFormat, data]);

  const renderValueWithSearch = useCallback((value: any, items: any, elements: any) => {
    if (_.isFunction(renderValue)) return renderValue(value, items, elements);

    let content: React.ReactElement;
    if (_.isArray(items)) {
      // (Check)TreePickerrenderValueitemslabellabelKeyname
      const labels = isTree
        ? items.map(i => _.last(_.get(i, 'name', '').split(' * ')))
        : items.map(i => _.get(i, 'label', '')).filter(i => i !== '');
      const count = labels.length;
      content = (
        <>
          <span className="rs-picker-value-list">{labels.join(', ')}</span>
          <span className="rs-picker-value-count" title={count.toString()}>
            {count > 99 ? '99+' : count}
          </span>
        </>
      );
    } else {
      content = isTree ? _.last(_.get(items, 'name', '').split(' * ')) : _.get(items, 'label', '');
    }

    return content;
  }, []);

  const renderMenuItemWithSearch = useCallback(
    (label, item) => {
      if (_.isFunction(renderMenuItem)) return renderMenuItem(label, item);

      const labelValue = _.get(item, 'label', '');
      return <span {...renderNodeWithTitle && { title: labelValue }}>{labelValue}</span>;
    },
    [renderMenuItem, renderNodeWithTitle]
  );

  const renderTreeNodeWithSearch = useCallback(
    (item: any) => {
      if (_.isFunction(renderTreeNode)) return renderTreeNode(item);

      const labelValue = _.get(item, 'label', '');
      return <span {...renderNodeWithTitle && { title: labelValue }}>{labelValue}</span>;
    },
    [renderTreeNode, renderNodeWithTitle]
  );

  const result: any = _.isEmpty(searchKeys)
    ? {
        labelKey,
        valueKey,
        data,
        isTree,
        renderValue,
        ...props
      }
    : {
        labelKey: 'name',
        valueKey: 'value',
        data: transformedData,
        isTree,
        renderValue: renderValueWithSearch,
        ...props
      };

  if (isTree) {
    result.childrenKey = _.isEmpty(searchKeys) ? childrenKey : 'children';
    result.renderTreeNode = _.isEmpty(searchKeys) ? renderTreeNode : renderTreeNodeWithSearch;
  } else {
    result.renderMenuItem = _.isEmpty(searchKeys) ? renderMenuItem : renderMenuItemWithSearch;
  }

  return result;
};

export default useSearchForPicker;
