import React, { useCallback } from 'react';
import _ from 'lodash';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

interface MultiLineCellProps<T> extends BaseCellProps<T> {
  omit?: 'middle' | 'start' | 'end';
  disableTips?: boolean;
  maxLine?: number;
  formatter?: (data?: any, rowData?: any) => any;
}

/**
 * 
 * @param formatter 
 * @param maxLine 
 * @param omit 'start', 'middle', 'end'
 * @param renderActions
 * @param disableTips Hover Tips
 * @param props
 */
export default React.memo(
  ({
    rowData,
    dataKey,
    formatter,
    maxLine,
    omit = 'middle',
    disableTips = false,
    ...props
  }: MultiLineCellProps<any>) => {
    //  
    const calcData = useCallback(() => {
      const data = _.get(rowData, dataKey, []); // dataKey
      const formattedData = _.isFunction(formatter) ? formatter(data, rowData) : data; // format

      const completeArray = _.isArray(formattedData)
        ? formattedData.reduce(
            (acc, i) => (!_.isNumber(i) && _.isEmpty(i) ? acc : acc.concat(i)),
            []
          ) // undefinednull
        : (formattedData && [String(formattedData)]) || []; // , false

      let renderArray = completeArray.slice(); // 
      const ellipsisSymbol = '...';
      if (_.isNumber(maxLine) && renderArray.length > maxLine) {
        const omittedLength = renderArray.length - maxLine + 1;
        switch (omit) {
          case 'start':
            renderArray.splice(0, omittedLength, ellipsisSymbol);
            break;
          case 'end':
            renderArray.splice(maxLine - 1, omittedLength, ellipsisSymbol);
            break;
          case 'middle':
          default:
            renderArray.splice(Math.ceil((maxLine - 1) / 2), omittedLength, ellipsisSymbol);
        }
      }

      return {
        renderArray,
        completeArray
      };
    }, [rowData, dataKey, formatter, maxLine, omit]);

    const hoverTipTitleGetFn = useCallback(() => {
      const { completeArray } = calcData();
      return _.isEmpty(completeArray) || disableTips
        ? undefined
        : completeArray.map((i: any) => (
            <div key={i} style={{ textAlign: 'left' }}>
              {i}
            </div>
          ));
    }, [calcData, disableTips]);

    const renderContent = useCallback(() => {
      const { renderArray } = calcData();
      return (
        <div>
          {!_.isEmpty(renderArray) ? (
            renderArray.map((i: any) => (
              <div key={i} className="wrapped-line">
                {i}
              </div>
            ))
          ) : (
            <div className="wrapped-line">{DEFAULT_TABLE_CELL_CONTENT}</div>
          )}
        </div>
      );
    }, [calcData]);
    return (
      <Cell
        dataKey={dataKey}
        rowData={rowData}
        renderTitle={hoverTipTitleGetFn}
        renderContent={renderContent}
        {...props}
      />
    );
  }
);
