import _ from 'lodash';

import { flattenNodes } from '@/utils/tree';
import { __PUBLIC__, __DEBUG__ } from '@/constants/config';
import { getUserProfile, getTrimmedUrl, getViewId } from '@/utils/location';
import * as menu from '@/constants/MenuConfig';

import { transformUrlToReg, validUrlToCommonUrl } from './utils';

// pathname  allowUrls allowUrls userInfo
export function validateUrl(pathname: any, allowUrls?: any) {
  if (__DEBUG__) { // DEV
    return true;
  }
  const userProfile = getUserProfile();
  if (pathname === '/' || _.startsWith(pathname, '/error') || _.startsWith(pathname, '/hac')) { // hac
    return true;
  }

  if ( // id  
    /^\/campaigns\/[0-9]+\/?$/.test(pathname) ||
    /^\/cross-campaigns\/[0-9]+\/?$/.test(pathname)
  ) {
    return true;
  }

  if (/^\/summary\/?$/.test(pathname)) { // 
    return userProfile.internal ? true : 403;
  }

  // Menuurl
  const availableUrls = _.map(
    flattenNodes([...menu.userMenu, ...menu.campaignMenu, ...menu.crossCampaignMenu], 'link').filter(i => i),
    link => _.isString(link as Url) ? link : link?.getRaw()
  );

  // url
  const validUrls = allowUrls || _.get(userProfile, 'validUrls');

  // url404
  const isAvailable = _.some(availableUrls, url => {
    const availableReg = new RegExp(transformUrlToReg(url));
    return availableReg.test(pathname);
  });
  if (!isAvailable) {
    return 404;
  }

  // urlurl
  let result: number | boolean = 403;
  for (let i = 0; i < validUrls.length; i++) {
    const validUrlReg = new RegExp(transformUrlToReg(validUrls[i]));
    // 
    if (validUrlReg.test(pathname) ||
      _.startsWith(pathname, `${validUrlToCommonUrl(validUrls[i], getViewId()?.toString())}/`)) {
      result = true;
      break;
    }
  }

  return result;
}

export function validateAuth(pathname: string, validUrls?: Array<string>) {
  if (!(window.location.pathname.startsWith(__PUBLIC__))) {
    return 404;
  }

  // DEV
  if (__DEBUG__) {
    return true;
  }

  const allow = validateUrl(getTrimmedUrl(pathname), validUrls);
  if (allow !== true) {
    console.log(pathname, '');
  }

  return allow;
}
