import React, { useMemo } from 'react';

import { generateComponent } from '@/components/Layouts/NormalLayout';
import { campaignMenu } from '@/constants/MenuConfig';

import { CommonCampaignRoutes, CampaignType } from '../common';

const pathToStringUrlParam = { campaignId: ':campaignId' };

export default () => {
  // react-routerswitch
  const campaignRoutes = useMemo(() => generateComponent(campaignMenu, pathToStringUrlParam), []);

  return <CommonCampaignRoutes type={CampaignType.CAMPAIGN} routesConfig={campaignRoutes} />;
};
