import _ from 'lodash';
import { replaceLocation, getTrimmedUrl } from './location';

const isExclude = (value: any) => value === undefined || value === '' || value === null;

export default function download(url: Url, params: BaseObject<any>) {
  //  url 
  if (typeof url === 'object') {
    //  delete  clone 
    params = _.cloneDeep(params);
    const keys = url.getKeys();
    url = url.toStringURL(params);
    keys.forEach(key => delete params[key]);
  }
  if (params && Object.keys(params).length > 0) {
    const esc = encodeURIComponent;
    const paramArray = [];
    // eslint-disable-next-line guard-for-in, no-restricted-syntax
    for (let key in params) {
      // =>string
      if (Array.isArray(params[key])) {
        let str = [] as any;
        // eslint-disable-next-line array-callback-return
        params[key].map((value: any) => {
          !isExclude(value) && str.push(`${esc(value)}`);
        });
        if (!_.isEmpty(str)) {
          paramArray.push(`${esc(key)}=${str.join(',')}`);
        }

        // eslint-disable-next-line no-continue
        continue;
      }
      !isExclude(params[key]) && paramArray.push(`${esc(key)}=${esc(params[key])}`);
    }
    // url = `${url}?${paramArray.join('&')}`;
    url = decodeURIComponent(`${url}?${paramArray.join('&')}`);
  }

  replaceLocation(url);

  //  subPath  / 

  // const { origin } = window.location;
  // if (_.startsWith(url, 'http')) {
  //   window.location.replace(`${origin}${getTrimmedUrl(url.replace(origin, ''))}`);
  // } else {
  //   const subPath = getTrimmedUrl(url);
  //   window.location.replace(`${origin}${subPath}`);
  // }
}
