import React, { useCallback } from 'react';
import _ from 'lodash';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';

import { localeKeys } from '@/locales/index';
import useIntl from '@/components/hooks/useIntl';

export const extractI18NKey = (id: string) => _.get(id.match(/i18n_(.*)/), '1', null);

// string formvalid
export default (id: string, values?: Record<string, ReactIntl.MessageValue | JSX.Element>): any => {
  const i18nKey = extractI18NKey(id) || id;
  return <FormattedMessage id={i18nKey} values={values} />;
};

/**
 * formatHTML
 */
export function fmtHTMLMsgCompFori18n(
  id: localeKeys,
  values?: Record<string, ReactIntl.MessageValue>
) {
  const i18nKey = extractI18NKey(id) || id;
  return <FormattedHTMLMessage id={i18nKey} values={values} />;
}

/**
 * string
 */
export type FormatMessageFnProps = (
  id: string,
  values?: Record<string, ReactIntl.MessageValue>,
  skipNoi18nPrefix?: boolean
) => string;
export function useFmtMsgFori18n() {
  const { formatMessage, ...rest } = useIntl();
  const fmtMsg = useCallback(
    (
      id: localeKeys = '',
      values?: Record<string, ReactIntl.MessageValue>,
      skipNoi18nPrefix = false
    ) => {
      if (!id) {
        // react-intl 
        return '';
      }

      const i18nKey = extractI18NKey(id);
      if (skipNoi18nPrefix && i18nKey === null) {
        // i18ni18n
        return id;
      }

      return formatMessage({ id: i18nKey || id }, values);
    },
    [formatMessage]
  );

  return {
    ...rest,
    formatMessage: fmtMsg
  };
}

interface UsePickerDataFromObjConfig {
  labelKey?: string;
  formatLabel?: (l: string, currentItem?: Record<string, any>, recursionLevel?: number) => string;
  recursionChildKey?: string;
  skipNoi18nPrefix?: boolean;
  values?: Record<string, ReactIntl.MessageValue>;
  notFormat?: boolean;
}

type DataObj = Array<Record<string, any>>;

/**
 * key-valueobjlabelKeyi18n_localekey
 * labelformatLabelkey
 *  recursionChildKey,recursion
 * 
 *
 *  [labelKey]keyvalue
 *
 * formatkey
 * format NkeyN
 */
export function usePickerDataFromObj(
  dataArray: DataObj,
  configObj = {} as UsePickerDataFromObjConfig
): typeof dataArray {
  const {
    labelKey = 'name',
    formatLabel,
    recursionChildKey,
    skipNoi18nPrefix = false,
    values,
    notFormat = false
  } = configObj;
  const { formatMessage } = useFmtMsgFori18n();

  const fmt = useCallback(
    (data: DataObj, recursionLevel = 0) =>
      _.map(data, d => {
        const text = notFormat ? d[labelKey] : formatMessage(d[labelKey], values, skipNoi18nPrefix);
        const result = _.isFunction(formatLabel) ? formatLabel(text, d, recursionLevel) : text;
        const recursionChildren = (recursionChildKey &&
          d[recursionChildKey] &&
          fmt(d[recursionChildKey], recursionLevel + 1)) as DataObj;
        return {
          ...d,
          [labelKey]: result,
          ...(recursionChildKey ? { [recursionChildKey]: recursionChildren } : null)
        };
      }),
    [formatMessage, formatLabel, recursionChildKey, skipNoi18nPrefix, values, notFormat, labelKey]
  );

  return fmt(dataArray);
}

export function getDict(key: string) {
  return _.get(key.match(/i18n_(.*)/), '1', 'mockCol');
}
