import _ from 'lodash';
import { getEnvFromSession } from '@/utils/env';

/**
 *  title
 * @param title
 */
export default (title: string | null) => {
  const { project } = getEnvFromSession();
  //  env  title 
  if (!project) {
    return title;
  }

  return _.isEmpty(title) ? project : title;
};
