import _ from 'lodash';

/**
 * templatevalidUrl /users/${'userId'}/campaigns
 * url /users/:id/campaigns 
 */

//  /users/${'userId'}/campaigns => ^\/users\/[^/]+\/campaigns$
export const transformUrlToReg = (url: string) =>
  `^${url.replace(/\//g, '\\/').replace(/\${[^/]+}/g, '[^/]+')}$`;

//  /users/${'userId'}/campaigns => /users/REPLACE_TXT/campaigns
export const validUrlToCommonUrl = (validateStr: string, replaceTxt = ':id') => {
  const paramsReg = /\${[^/]+}/g;
  return validateStr.replace(paramsReg, replaceTxt);
};

//  /users/${'userId'}/campaigns
export const feUrlToCommonUrl = (link: string | APIDictObject) => {
  const paramsReg = /\/\//g;
  if (_.isString(link) || !link) {
    return link;
  }

  // /users/${'userId'}/campaigns => /users//campaigns
  const linkNoParam = link.toStringURL && link.toStringURL({ userId: '', campaignId: '' });
  // /users//campaigns => /users/:id/campaigns
  return link && _.replace(linkNoParam, paramsReg, '/:id/');
};

//  /users/${userId}/campaigns  /users/${'userId'}/campaigns
export const testPathnameMatchRawUrl = (rawUrl: string, pathname: string) => {
  if (!rawUrl) {
    return false;
  }

  // eslint-disable-next-line prefer-template
  return new RegExp(validUrlToCommonUrl(rawUrl, '[^/]+') + '(/.+)?$') // => /users/${userId}/campaigns
    .test(pathname);
};
