type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

// Recompose.composePropMapper
const composer = <T, K>(...mappers: PropMapper[]): PropMapper<T, K> => props => {
  return mappers.reduce((acc, mp) => mp && mp(acc), props) as K;
};

export default composer;
