import { FilterRowValue } from './FilterPanel/FilterRow';
import { PickerAsyncDataCtxProps } from './context';

import { FormatMessageFnProps } from '@/utils/formatMessage';

export type AdvFilterValue = Array<FilterRowValue & { id?: string }>;
export type AdvFilterInnerValue = Array<FilterRowValue & { id: string }>;

export interface FilterInnerProps {
  filterVal: AdvFilterInnerValue;
  setFilterVal: React.Dispatch<React.SetStateAction<(FilterRowValue & { id?: string })[]>>;
  filterData: Array<FilterNodeConfig>;
  asyncData: PickerAsyncDataCtxProps;
  limitNum: number;
  onSubmit: (val: AdvFilterInnerValue) => void;
}

export enum FILTER_OPERATION { // filter
  ANY = 'any',
  EQUAL = 'equal',
  CONTAIN = 'contain'
}

export enum FILTER_TYPE { // filterselect
  CHECK = 'check',
  SELECT = 'select',
  CASCADER = 'cascader',
  INPUT = 'input'
}

export type TagFormatterEntity = { label: string; removeKey?: string };

export type TagFormatterFn = (
  node: FilterNodeConfig,
  value: any,
  a: PickerAsyncDataCtxProps,
  fmtFn: FormatMessageFnProps
) => Array<TagFormatterEntity>;

export interface FilterNodeConfig {
  key: string;
  label: string;
  operation: FILTER_OPERATION;
  type: FILTER_TYPE;
  asyncFnKey?: Array<any>; // async
  data?: Array<any>; // async  input 
  defaultValue: Array<any> | string;
  tags?: string;
  labelKey?: string; // defaultTo name
  valueKey?: string; // defaultTo id
  childrenKey?: string;
  i18nFormatKey?: string;
  valueInnerToOuter?: (data: Array<any>) => Array<any>; // called on onChange callback and pass data to caller
  valueOuterToInner?: (data: Array<any>) => Array<any>; // called when parsing value and store the formatted value
  mapLabel?: (node: any) => string;
  // tag paneldefaultValue tagidx idx string
  tagFormatter?: TagFormatterFn; //  labelvalue
}

export const operatorData = {
  [FILTER_OPERATION.ANY]: [{ id: 'any', name: 'i18n_any' }],
  [FILTER_OPERATION.CONTAIN]: [
    { id: 'contain', name: 'i18n_contain' },
    { id: 'equal', name: 'i18n_equalTo' }
  ],
  [FILTER_OPERATION.EQUAL]: [{ id: 'equal', name: 'i18n_equalTo' }]
};

export const defaultLabelKey = 'name';
export const defaultValueKey = 'id';
export const defaultChildrenKey = 'children';
