// css
function getStyle(node, name) {
  if (!node) {
    return;
  }
  if (node.currentStyle) {
    // IE/OP
    return node.currentStyle[name];
  } // FF/CM
  return node.ownerDocument.defaultView.getComputedStyle(node, null)[name];
}

/**
 *
 * @param {*} fileURL URL 
 * @param {*} success 
 * @param {*} fail 
 */
export default function loadCssFile(fileURL, success, fail) {
  const container = document.getElementsByTagName('head')[0];
  const link = document.createElement('link');
  link.rel = 'stylesheet';
  link.type = 'text/css';
  link.media = 'screen';
  link.href = fileURL;
  container.appendChild(link);

  // divcss
  const checkTimer = setInterval(function() {
    const node = document.getElementById('detected');
    const height = getStyle(node, 'height');
    // css
    if (height === '1px') {
      clearInterval(checkTimer);
      clearTimeout(timeoutTimer);
      success && success();
    }
  }, 50);

  // css
  const timeoutTimer = setTimeout(function() {
    clearInterval(checkTimer);
    clearTimeout(timeoutTimer);
    fail && fail();
  }, 5000);
}
