/* eslint-disable no-continue */

import _ from 'lodash';

interface PickerDataItemType {
  label: string;
  value: string;
  name?: string;
  children?: PickerDataItemType[];
  [key: string]: any;
}

export interface PickerDataConfig {
  labelKey?: string;
  valueKey?: string;
  childrenKey?: string;
  nameKeys?: string[];
  noSplice?: boolean;
  mapLabel?: (node: Record<string, any>) => string;
}

export interface MapConfig {
  idKey?: string;
  childrenKey?: string;
  idType?: 'number' | 'string';
  mergeChildren?: boolean;
  simplifyLeaves?: boolean;
}

/**
 * picker dataidid
 * [{id: 1, name: 'a', children: [{id: 1, name: 'aa'}]}]  [{value: '1', label: 'a', children: [{value: '1+1', label: 'aa'}]}]
 * @param source 
 * @param labelKey sourcelabel
 * @param valueKey sourcevalue
 * @param childrenKey sourcechildren
 * @param nameKeys name*
 * @param noSplice value
 * @param mapLabel label
 */
export function toPickerData(
  source: Record<string, any>[] = [],
  {
    labelKey = 'label',
    valueKey = 'value',
    childrenKey = 'children',
    nameKeys = [],
    noSplice = false,
    mapLabel
  }: PickerDataConfig = {}
): PickerDataItemType[] {
  const dest: any = { children: [] }; // 
  const stack = _.cloneDeep(source)
    .slice()
    .reverse();

  // source
  while (!_.isEmpty(stack)) {
    const currentNode = stack.pop();

    // 
    if (_.isNil(currentNode[labelKey]) || _.isNil(currentNode[valueKey])) {
      continue;
    }

    const { parentNode } = currentNode; // 

    // 
    const destNode: PickerDataItemType = {
      label: _.isFunction(mapLabel) ? mapLabel(_.cloneDeep(currentNode)) : currentNode[labelKey],
      value: parentNode
        ? `${parentNode.value}+${currentNode[valueKey]}`
        : currentNode[valueKey].toString()
    };
    noSplice && (destNode.value = currentNode[valueKey]);

    // nameKeysname
    _.isEmpty(nameKeys) ||
      (destNode.name = parentNode
        ? `${parentNode.name} * ${nameKeys.map(key => currentNode[key]).join(' * ')}`
        : nameKeys.map(key => currentNode[key]).join(' * '));

    // parentNode
    if (_.isArray(currentNode[childrenKey])) {
      destNode.children = [];
      _.forEachRight(currentNode[childrenKey], childNode => {
        childNode.parentNode = destNode;
        stack.push(childNode);
      });
    }

    // 
    (parentNode || dest).children.push(destNode);
  }

  return dest.children;
}

/**
 * pickerpickervalue
 * [{id: 1, children: [2, 3]}, {id: 4, children: []}]  ['1+2', '1+3', '4']
 * @param source 
 * @param data PickerdatatoPickerData
 * @param idKey sourceidid
 * @param childrenKey sourcechildrenchildren
 */
export function mapParams2PickerValue(
  source: Record<string, any> = [],
  data: PickerDataItemType[] = [],
  { idKey = 'id', childrenKey = 'children' }: MapConfig = {}
) {
  const dest = [];
  const stack: any[] = [
    {
      [childrenKey]: _.cloneDeep(source),
      relatedNode: {
        children: _.cloneDeep(data)
      }
    }
  ];

  while (!_.isEmpty(stack)) {
    const currentNode = stack.pop();
    const { parentNode } = currentNode;

    // pickerData
    const relatedNode =
      currentNode.relatedNode ||
      _.get(parentNode, 'relatedNode.children', []).find((child: PickerDataItemType) => {
        const dataValue = _.get(child, 'value');
        const srcId = String(_.get(currentNode, [idKey]));
        return !_.isNil(dataValue) && (dataValue === srcId || _.endsWith(dataValue, `+${srcId}`));
      });

    // 
    // dest
    if (
      !relatedNode ||
      _.some(dest, i => i === relatedNode.value || _.startsWith(relatedNode.value, `${i}+`))
    ) {
      continue;
    }

    currentNode.relatedNode = relatedNode;

    // 
    if (!_.isEmpty(currentNode[childrenKey])) {
      _.forEachRight(currentNode[childrenKey], (child, idx) => {
        const newChild = {
          [idKey]: _.isObject(child) ? (child as any)[idKey] : child, // 
          [childrenKey]: child[childrenKey],
          parentNode: currentNode // 
        };
        stack.push(newChild); // 
        currentNode[childrenKey][idx] = newChild;
      });
      continue;
    }

    // 
    let mergingNode = currentNode;
    while (mergingNode.parentNode) {
      const relatedChildren = _.get(mergingNode, 'relatedNode.children', []); // children
      const relatedValue = _.get(mergingNode, 'relatedNode.value');
      const mergingChildren = _.get(mergingNode, childrenKey);
      mergingNode = mergingNode.parentNode;

      // children
      if (_.isEmpty(relatedChildren) || _.isEmpty(mergingChildren)) {
        _.isNil(relatedValue) || dest.push(relatedValue);
        continue;
      }

      // destrelatedChildren
      const allIncluded = _.isEmpty(_.difference(_.map(relatedChildren, 'value'), dest));

      // relatedValueallIncluded
      if (_.isNil(relatedValue)) {
        allIncluded && _.remove(dest, i => true);
      } else if (allIncluded) {
        // destchildren valuevalue
        _.remove(dest, i => _.startsWith(i, `${relatedValue}+`));
        dest.push(relatedValue);
      } else {
        break;
      }
    }
  }

  return dest;
}

/**
 * mapParams2PickerValuepickervalue
 * ['1+2', '1+3', '4']  [{id: 1, children: [2, 3]}, {id: 4, children: []}]
 * @param source 
 * @param data PickerdatatoPickerData
 * @param idKey id
 * @param childrenKey children
 * @param idType idnumberstring
 * @param simplifyLeaves id
 *        true[{id: 1, children: [2, 3]}]
 *        false[{id: 1, children: [{id: 2}, {id: 3}]}]
 * @param mergeChildren (true)(false)
 *        
 */
export function mapPickerValue2Params(
  source: string[] = [],
  data: PickerDataItemType[] = [],
  {
    idKey = 'id',
    childrenKey = 'children',
    idType = 'number',
    simplifyLeaves = true,
    mergeChildren = true
  }: MapConfig = {}
) {
  const dest: Record<string, any> = {}; // 
  const src = _.cloneDeep(source).sort((x, y) => x.length - y.length);
  const pickerData = _.cloneDeep(data);

  // node.childrendestId
  // node.children{id, children: []}
  const mapChildren = (id: MapConfig['idType'], node: any) => {
    const stack = _.get(node, 'children', [])
      .slice()
      .reverse();
    const result: any = {
      [idKey]: id,
      [childrenKey]: []
    };

    while (!_.isEmpty(stack)) {
      const currentNode = stack.pop();
      const currentValue = _.last(_.get(currentNode, 'value', '').split('+'));
      const destId = idType === 'string' ? String(currentValue) : Number(currentValue);
      let destNode: any;

      if (_.isArray(currentNode.children)) {
        destNode = {
          [idKey]: destId,
          [childrenKey]: []
        };
        _.forEachRight(currentNode.children.slice(), childNode => {
          childNode.parentNode = destNode;
          stack.push(childNode);
        });
      } else {
        destNode = simplifyLeaves ? destId : { [idKey]: destId };
      }

      _.get(currentNode.parentNode || result, childrenKey).push(destNode);
    }

    return result;
  };

  _.forEach(src, value => {
    const idArr = value.split('+');
    let parentNodeInDest = dest; // 
    let parentNodeInData: Record<string, any> = { children: pickerData, relatedNode: dest }; // picker data
    let haveFoundParent = false; // dest
    let newNode; // 
    let parentNodeInNew; // 

    // destparentNodeInDest
    // picker dataparentNodeInData
    for (let i = 0; i < idArr.length; i += 1) {
      const splicedId = idArr.slice(0, i + 1).join('+');
      const destId = idType === 'string' ? String(idArr[i]) : Number(idArr[i]);
      const relatedNodeInData = _.get(parentNodeInData, 'children', []).find(
        // idpicker data
        (child: any) => child.value === splicedId
      );
      const relatedNodeInDest = _.get(parentNodeInDest, childrenKey, []).find(
        // iddest
        (child: any) => _.get(child, idKey, child) === destId
      );

      // picker data
      if (!relatedNodeInData) {
        newNode = null;
        break;
      }

      // dest
      if (!haveFoundParent) {
        // mergeChildrenparentchildren
        if (mergeChildren && _.get(parentNodeInDest, 'children.length') === 0) {
          newNode = null;
          break;
        }

        // dest
        if (relatedNodeInDest) {
          relatedNodeInData.parentNode = parentNodeInData;
          relatedNodeInData.relatedNode = relatedNodeInDest;
          parentNodeInData = relatedNodeInData;
          parentNodeInDest = relatedNodeInDest;
          continue;
        }

        // dest
        // parentNodeInDestdest
        haveFoundParent = true;
        parentNodeInNew = { [childrenKey]: [] };
      }

      // id
      if (i !== idArr.length - 1) {
        const destNode: any = {
          [idKey]: destId,
          [childrenKey]: []
        };
        _.isNil(newNode) && (newNode = destNode);
        parentNodeInNew[childrenKey].push(destNode);
        parentNodeInNew = destNode;
        relatedNodeInData.parentNode = parentNodeInData;
        relatedNodeInData.relatedNode = destNode;
        parentNodeInData = relatedNodeInData;
        continue;
      }

      // id
      // picker datasimplifyLeaf
      if (_.isNil(relatedNodeInData.children)) {
        const destNode = simplifyLeaves ? destId : { [idKey]: destId };
        _.isNil(newNode) && (newNode = destNode);
        parentNodeInNew[childrenKey].push(destNode);
        relatedNodeInData.parentNode = parentNodeInData;
        relatedNodeInData.relatedNode = destNode;
      } else {
        // mergeChildren
        const destNode = mergeChildren
          ? {
              [idKey]: destId,
              [childrenKey]: []
            }
          : mapChildren(destId as MapConfig['idType'], relatedNodeInData);
        _.isNil(newNode) && (newNode = destNode);
        parentNodeInNew[childrenKey].push(destNode);
        relatedNodeInData.parentNode = parentNodeInData;
        relatedNodeInData.relatedNode = destNode;
      }
    }

    // newNodevalue
    if (_.isNil(newNode)) {
      return;
    }

    parentNodeInDest[childrenKey] || (parentNodeInDest[childrenKey] = []);
    parentNodeInDest[childrenKey].push(newNode);

    // mergeChildrennewNode
    if (!mergeChildren) {
      return;
    }

    // mergeChildren, 
    while (parentNodeInData.parentNode) {
      const relatedNodeInDest = parentNodeInData.relatedNode;
      const childrenInDest = _.get(relatedNodeInDest, childrenKey);
      const childrenInData = _.get(parentNodeInData, 'children');

      // childrenInDestchildrenInData
      // 
      // // childrenInDatarelatedNoderelatedNode.childrenempty
      // childrenInDestmerge
      const allChosen =
        _.get(childrenInDest, 'length') === 0 ||
        _.every(
          childrenInData,
          child => !_.isNil(child.relatedNode) && _.isEmpty(child.relatedNode[childrenKey])
        );

      if (!allChosen) {
        break;
      }

      relatedNodeInDest[childrenKey] = [];
      parentNodeInData = parentNodeInData.parentNode;
    }
  });

  return dest[childrenKey] || [];
}
