import React, { SyntheticEvent, useCallback, useMemo, useRef, useState } from 'react';
import _ from 'lodash';
import { Alert, Button } from 'rsuite';
import Toggle from '@/components/Form/Toggle';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { API_CAMPAIGN, API_CROSS_CAMPAIGN, API_TEAM } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import template from '@/utils/template';
import RESPONSE_STATUS from '@/constants/responseStatus';
import { BETeamPicker } from '@/components/Table/Cells/FloatActionCell/Actions';

// rowDatateamsBETeam
// teamsobjectBE IDkeyarray
// beIdteamIdteamNamebeIdBE All Members
interface Props {
  isCross: boolean;
  rowData: {
    id: number;
    teams: {
      [beId: number]: Array<{
        beName: string;
        teamId?: number;
        teamName?: string;
      }>;
    };
  };
  refetchTableData: Function;
}

// rowData.teams
type RowTeamNode = [
  {
    beId: number;
    beName: string;
  },
  Array<{
    id: number;
    name: string;
  }>
];

type TeamDataNode = { id: number; name: string };
interface BEDataNode {
  id: number;
  name: string;
  teams?: TeamDataNode[];
}

interface BEValueNode {
  id: number;
  teams: number[];
}

export default React.memo(({ isCross, rowData, refetchTableData }: Props) => {
  const pickerRef = useRef(null);
  const { formatMessage } = useFmtMsgFori18n();

  const [pickerData, setPickerData] = useState<BEDataNode[]>([]); // 
  const [pickerValue, setPickerValue] = useState<BEValueNode[]>([]); // 
  const [originalPickerValue, setOriginalPickerValue] = useState<BEValueNode[]>([]); // 
  const [allBEIds, setAllBEIds] = useState<number[]>([]); // AllBE ID
  const [originalAllBEIds, setOriginalAllBEIds] = useState<number[]>([]); // AllBE ID
  const [tempValue, setTempValue] = useState<BEValueNode[]>([]); // AllBEteams
  const [pickerLoading, setPickerLoading] = useState(true); // picker

  const { onFetch: onFetchAvailableBETeams } = useFetch<CommonResponse>(
    FETCH_METHOD.GET,
    API_TEAM,
    {}
  );

  const { onFetch: onSubmit, loading: submitLoading } = useFetch<CommonResponse>(
    FETCH_METHOD.PUT,
    template`${isCross ? API_CROSS_CAMPAIGN : API_CAMPAIGN}/${'id'}`,
    {
      params: { id: rowData.id }
    }
  );

  // 
  const handleOpen = useCallback(() => {
    setPickerLoading(true);
    onFetchAvailableBETeams({
      params: { campaignId: rowData.id }
    }).then(rsp => {
      const rspData: BEDataNode[] = _.get(rsp, 'data.records', []);
      // rowData.teams
      const chosenValueInRow: RowTeamNode[] = _.toPairs(_.get(rowData, 'teams', [])).map(
        ([beId, teams]) => {
          const beName = _.get(teams, '0.beName');
          const teamId = _.get(teams, '0.teamId');
          return [
            { beId: Number(beId), beName },
            _.isNumber(teamId) // teamIdBEAll
              ? _.map(teams, i => ({
                  id: _.get(i, 'teamId'),
                  name: _.get(i, 'teamName')
                }))
              : []
          ];
        }
      );

      // chosenValueInRow
      // rspDataBETeamrspData
      // pickerValue, allBEIds
      const initPickerValue: BEValueNode[] = [];
      const initAllBEIds: number[] = [];
      _.forEach(chosenValueInRow, ([chosenBE, chosenTeams]) => {
        const beInRsp = rspData.find(i => i.id === chosenBE.beId);
        if (beInRsp) {
          // rspDataBEteamsrspData
          beInRsp.teams = _.unionBy(beInRsp.teams, chosenTeams, 'id');
        } else {
          // rspDataBErspDataBE
          rspData.push({
            id: chosenBE.beId,
            name: chosenBE.beName,
            teams: chosenTeams
          });
        }

        // pickerValue
        initPickerValue.push({
          id: chosenBE.beId,
          teams: _.map(chosenTeams, 'id')
        });

        // chosenTeamsBEAll
        if (_.isEmpty(chosenTeams)) {
          initAllBEIds.push(chosenBE.beId);
        }
      });

      setPickerData(rspData);
      setPickerValue(initPickerValue);
      setOriginalPickerValue(initPickerValue);
      setAllBEIds(initAllBEIds);
      setOriginalAllBEIds(initAllBEIds);
      setPickerLoading(false);
    });
  }, [onFetchAvailableBETeams, rowData]);

  // state
  const handleClose = useCallback(() => {
    setPickerData([]);
    setPickerValue([]);
    setOriginalPickerValue([]);
    setAllBEIds([]);
    setOriginalAllBEIds([]);
    setTempValue([]);
    setPickerLoading(true);
  }, []);

  const handleChange = useCallback(
    val => {
      // teamBE()All
      // BEBEAll
      const unCheckedBE = _.differenceBy(val, pickerValue, 'id')[0];
      const idx = pickerData.findIndex(i => i.id === _.get(unCheckedBE, 'id'));

      if (idx >= 0 && _.isEmpty(pickerData[idx].teams)) {
        return false;
      }

      setPickerValue(val);
    },
    [pickerData, pickerValue]
  );

  const handleChangeToggle = useCallback(
    beId => (val: boolean, e: SyntheticEvent) => {
      e.preventDefault();

      if (val) {
        // AllBEvalueBETeam
        setTempValue(prevState => [
          ...prevState,
          { id: beId, teams: _.get(pickerValue.find(i => i.id === beId), 'teams', []) }
        ]);
        setPickerValue(prevState => [
          ...prevState.filter(i => i.id !== beId),
          {
            id: beId,
            teams: _.map(_.get(pickerData.find(i => i.id === beId), 'teams', []), 'id')
          }
        ]);
        setAllBEIds(prevState => [...prevState, beId]);
      } else {
        // pickerValueBEteams; tempValues
        setPickerValue(prevState => {
          const teamsInTemp = _.get(tempValue.find(i => i.id === beId), 'teams');

          return [
            ...prevState.filter(i => i.id !== beId),
            ...(_.isEmpty(teamsInTemp)
              ? []
              : [
                  {
                    id: beId,
                    teams: teamsInTemp
                  }
                ])
          ];
        });
        setTempValue(prevState => prevState.filter(i => i.id !== beId));
        setAllBEIds(prevState => prevState.filter(i => i !== beId));
      }
    },
    [pickerData, pickerValue, tempValue]
  );

  // 
  // { teams: [{
  //   beId,
  //   teams: number[]
  // }]}
  // teams:
  // * BEAll
  // * teamIdBE
  // BEAllteams
  const handleSubmit = useCallback(() => {
    const submitData = pickerValue.map(({ id, teams }) => ({ beId: id, teams }));
    // AllBEteams
    allBEIds.forEach(beId => {
      const be = submitData.find(i => i.beId === beId);
      if (be) {
        be.teams = [];
      } else {
        submitData.push({
          beId,
          teams: []
        });
      }
    });

    onSubmit({
      data: { teams: submitData }
    }).then(rsp => {
      const { status } = rsp;
      if (status === RESPONSE_STATUS.FIELD_INVALID) {
        const errObj = _.get(rsp, 'errors', {});
        _.forOwn(errObj, (v, k) => {
          Alert.error(`${k}: ${v}`);
        });
        return;
      }

      refetchTableData && refetchTableData();
      const triggerHideFn = _.get(pickerRef, 'current.triggerRef.current.hide', null);
      _.isFunction(triggerHideFn) && triggerHideFn();
    });
  }, [allBEIds, onSubmit, pickerValue, refetchTableData]);

  // BEAll Toggle
  const renderTreeNode = useCallback(
    (nodeData: any) => {
      const beId = Number(nodeData.value);
      return (
        <span className={!nodeData.parentNode ? 'parent-node-content' : 'child-node-content'}>
          <span className="tree-node-text" title={nodeData.label}>
            {nodeData.label}
          </span>
          {!nodeData.parentNode && (
            <Toggle
              value={allBEIds.includes(beId)}
              checkedChildren="All"
              unCheckedChildren="All"
              onChange={handleChangeToggle(beId)}
            />
          )}
        </span>
      );
    },
    [allBEIds, handleChangeToggle]
  );

  const renderExtraFooter = useCallback(() => {
    // 
    const hasChanged =
      allBEIds.sort().toString() !== originalAllBEIds.sort().toString() ||
      !_.isEqual(pickerValue, originalPickerValue);
    return (
      <div className="ad-auth-table-picker-footer">
        <Button
          appearance="primary"
          size="sm"
          loading={submitLoading}
          onClick={handleSubmit}
          disabled={!hasChanged || pickerLoading}
        >
          {formatMessage('i18n_confirm')}
        </Button>
      </div>
    );
  }, [
    allBEIds,
    handleSubmit,
    originalAllBEIds,
    originalPickerValue,
    pickerValue,
    submitLoading,
    pickerLoading
  ]);

  // allBEIdspickerDatabeIdteamId
  // 
  const disabledItemValues = useMemo(
    () =>
      allBEIds.reduce((acc, beId) => {
        const beInData = pickerData.find(i => i.id === beId);
        return beInData
          ? [...acc, String(beId), ..._.map(beInData.teams, i => `${beId}+${i.id}`)]
          : acc;
      }, []),
    [allBEIds, pickerData]
  );

  return (
    <BETeamPicker
      ref={pickerRef}
      data={pickerData}
      value={pickerValue}
      childrenKey="teams"
      placement="auto"
      apppearance="subtle"
      formatConfig={{
        childrenKey: 'teams',
        mergeChildren: false // team id
      }}
      defaultExpandAll
      loading={pickerLoading}
      disabledItemValues={disabledItemValues}
      onOpen={handleOpen}
      onClose={handleClose}
      onChange={handleChange}
      renderTreeNode={renderTreeNode}
      renderExtraFooter={renderExtraFooter}
      menuClassName="ad-auth-menu"
    />
  );
});
