import React, { useMemo } from 'react';
import _ from 'lodash';
import { Switch } from 'react-router-dom';

import BasicRoute from '@/components/Routes/BasicRoute';
import NormalLayout, { generateComponent } from '@/components/Layouts/NormalLayout';
import { userMenu } from '@/constants/MenuConfig';
import { getValidMenus } from '@/utils/auth';
import { getUserProfile } from '@/utils/location';

import Base from './base';

interface UsersLayoutProps {
  children: React.ReactChildren | React.ReactChild;
}

const UsersLayout = React.memo(({ children }: UsersLayoutProps) => {
  // ready.jsuserInfo
  const menu = useMemo(() => {
    const validUrl = _.get(getUserProfile(), 'validUrls', []);
    return getValidMenus({}, userMenu, validUrl, true);
  }, []);

  return (
    <NormalLayout
      menuConfig={menu} // 
    >
      {children}
    </NormalLayout>
  );
});

const pathToStringUrlParam = { userId: ':userId', id: ':id' };
const userBasicRoute = generateComponent(userMenu, pathToStringUrlParam); // react-routerswitch
const Routes = React.memo(() => {
  return (
    <BasicRoute component={UsersLayout}>
      <Base>
        <Switch>{userBasicRoute}</Switch>
      </Base>
    </BasicRoute>
  );
});

export default Routes;
