import { usePickerDataFromObj } from '@/utils/formatMessage';
import { adsConfigData } from './constants';

/**
 * adsConfigDatalabel, labelSuffix, tableHeader, emptyLabel
 * labellabelSuffixlabel
 */

const formatLabel = (i18nStr: string, currentItem: Record<string, any>) => {
  return `${i18nStr}${currentItem.labelSuffix && `(${currentItem.labelSuffix})`}`;
};

export default () => {
  const adsConfigDataIntlWithLabelSuffix = usePickerDataFromObj(adsConfigData, {
    labelKey: 'labelSuffix',
    skipNoi18nPrefix: true
  });
  const adsConfigDataIntlWithLabel = usePickerDataFromObj(adsConfigDataIntlWithLabelSuffix, {
    labelKey: 'label',
    skipNoi18nPrefix: true,
    formatLabel
  });
  const adsConfigDataIntlWithTableHeader = usePickerDataFromObj(adsConfigDataIntlWithLabel, {
    labelKey: 'tableHeader',
    skipNoi18nPrefix: true
  });
  const adsConfigDataIntlWithEmptyLabel = usePickerDataFromObj(adsConfigDataIntlWithTableHeader, {
    labelKey: 'emptyLabel',
    skipNoi18nPrefix: true
  });
  return adsConfigDataIntlWithEmptyLabel;
};
